@import "./sass/variable.css";
@import "./sass/loading.css";
@import "./sass/header.css";
@import "./sass/footer.css";
@import "./sass/homePageCatalog.css";
@import "./sass/mainSection.css";
@import "./sass/tablet.css";
@import "./sass/mobileVersion.css";

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto;
}

* :active,
* :hover,
* :focus {
  outline: 0;
  outline-offset: 0;
}

button {
  padding: 0;
}

.arrow-to-top {
  display: none;
  position: fixed;
  bottom: 24px;
  right: 16px;
  width: 80px;
  height: 80px;
  background-color: #59bd5a;
  border: none;
  border-radius: 2px;
  background-image: url(./images/btn-up.png);
  background-repeat: no-repeat;
  background-position: center;
}

.arrow-displayed {
  display: block;
  animation-duration: 800ms;
  animation-name: slide;
}

@keyframes slide {
  from {
    width: 0;
    height: 0;
  }

  to {
    width: 80px;
  }
}

@media (max-width: 639px) {
  .arrow-to-top {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
